<template>
  <div class="portfolio-gallery__item" :data-id="category">
    <div class="portfolio-gallery__item__link-overlay">
      <!-- <a :href="page" style="display: block; height: 100%; width: 100%"> </a> -->
      <router-link :to="{ path: `${page}` }" style="display: block; height: 100%; width: 100%"></router-link>
    </div>
    <div class="portfolio-gallery__item__inner" style="text-align: left">
      <!-- <img src="../assets/img/portfolio/2.jpg" alt="portfolio" /> -->
      <img
        v-if="fileName"
        v-bind:src="require('@/assets/img/portfolio/thumbnails/' + fileName)"
      />
      <div class="portfolio-gallery__item__inner__title mt-3">{{ title }}</div>
      <div>
        {{ description }}
      </div>
      <div>
        <div
          v-for="(label, index) in labels"
          :key="index"
          class="portfolio-gallery__item__inner__label"
          :class="{ add_space_between: index > 0 }"
        >
          {{ label }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "PortfolioItem",
  props: {
    title: {
      type: String,
      required: false,
    },
    description: {
      type: String,
      required: false,
    },
    labels: {
      type: Array,
      required: false,
    },
    category: {
      type: String,
      required: false,
    },
    fileName: {
      type: String,
      required: false,
    },
    page: {
      type: String,
      required: false,
    },
  },
};
</script>