<template>
  <div class="container portfolio">
    <div class="row portfolio__header">
      <h1>
        Data Science Portfolio
        <span class="portfolio__header__icon"> üí° </span>
      </h1>
      <span>
        This page contains an overview of the projects on which I have worked.
        If you have opened this website on a computer, you can narrow down the
        projects by using the filters listed below. More information is
        available by clicking on an item.
      </span>
    </div>

    <div class="row portfolio__filters">
      <div class="portfolio__filters__buttons">
        <ul id="filter-btns">
          <li class="active" data-target="all">All</li>
          <li data-target="Analytics">Data Analytics</li>
          <li data-target="Generative AI">Generative AI</li>
          <li data-target="NLP">NLP</li>
        </ul>
      </div>
      <hr />
    </div>

    <div class="row">
      <div class="portfolio-gallery">
        <template v-for="item in portfolio_items">
          <!-- eslint-disable-next-line vue/require-v-for-key -->
          <!-- eslint-disable-next-line vue/valid-v-for-->
          <portfolio-item :title="item.title" :description="item.description" :labels="item.portfolio_l"
            :category="item.category" :fileName="item.fileName" :page="item.page">
          </portfolio-item>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import { BIcon, BIconInstagram } from "bootstrap-vue";
import PortfolioItem from "../components/PortfolioItem.vue";
import NprogressContainer from "vue-nprogress/src/NprogressContainer";

export default {
  name: "Create",
  components: { BIcon, BIconInstagram, PortfolioItem, NprogressContainer },
  data() {
    return {
      portfolio_labels: ["a", "b", "c"],
      portfolio_items: [
        {
          title: "Personal Attribute Extraction from Product Reviews",
          description:
            "A system that extracts personal attributes from product reviews to generate structured knowledge graph for deeper customer insights.",
          portfolio_l: ["NLP", "Text Mining", "Knowledge Graphs"],
          category: "Generative AI,NLP",
          page: "/triplet_extraction",
        },
        {
          title: "Synthetic AIS Data Generation for Maritime Routes",
          description:
            "Autoregressive sequence model to generate realistic synthetic vessel trajectories from AIS data.",
          portfolio_l: ["Simulation", "Generative AI", "Deep Learning", "Geospatial Data"],
          category: "Generative AI",
          page: "/synthetic_data",
        },
        {
          title: "Generative AI for Predictive Maintenance",
          description:
            "Project about the use of generative AI and NLP to enhance predictive maintenance at Bonfiglioli by integrating unstructured data sources",
          portfolio_l: ["Generative AI", "Predictive Analytics", "NLP"],
          category: "Data Science,AI,Generative AI,Predictive Maintenance",
          page: "/bonfiglioli",
        },
        {
          title: "Data-driven Inventory Management",
          description:
            "A data-driven tool that analyzes AEMICS' inventory to provide real-time insights, forecasts, and automated ERP integration.",
          portfolio_l: ["Data Analytics", "Automation", "Visualization"],
          category: "Analytics",
          page: "/inventory_analyzer",
        }
      ],
    };
  },
  mounted() {
    const filterButtons = document.querySelector("#filter-btns").children;
    const items = document.querySelector(".portfolio-gallery").children;

    for (let i = 0; i < filterButtons.length; i++) {
      filterButtons[i].addEventListener("click", function () {
        for (let j = 0; j < filterButtons.length; j++) {
          filterButtons[j].classList.remove("active");
        }
        this.classList.add("active");
        const target = this.getAttribute("data-target");

        for (let k = 0; k < items.length; k++) {
          const categories = items[k].getAttribute("data-id").split(",");
          items[k].style.display = "none";
          if (categories.includes(target) || target === "all") {
            items[k].style.display = "block";
          }
        }
      });
    }
  },
};
</script>
